(function($) {

    'use strict';

    var anchorSmoothScroll = function() {
        
        $("a[href*='#']:not([href='#'])").click(function(e) {
			e.preventDefault();
			
            var hash = this.hash;
            var section = $(hash);
          
            if (hash) { 
              $('html, body').animate({
                scrollTop: (section.offset().top)
              }, 800, 'swing', function(){
                history.replaceState({}, "", hash);
              });
			}
			
          });

    }

    var anchorLoadSmoothScroll = function() {

        var hash = window.location.hash

        if ( hash == '' || hash == '#' || hash == undefined ) return false;
            
        var target = $(hash);
        
        target = target.length ? target : $('[name=' + this.hash.slice(1) +']');
        
        if (target.length) {
            $('html,body').stop().animate({
            scrollTop: target.offset().top
            }, 500, 'swing');
            
        }

    }
    
    var titleVisible = function() {
        $(window).scroll(function() {
            $(".divider").each(function() {
                if(($(this).offset().top - $(window).height()*0.9) <= $(window).scrollTop()){
                    $(this).find(".divider__title").addClass("visible");
                }
            })
        }).scroll();
    }

    var topVisible = function() {
        $(window).scroll(function() {
            if($(window).outerHeight()*0.8 < $(window).scrollTop()){
                $("#backToTop").addClass("visible");
            } else {
                $("#backToTop").removeClass("visible");
            }
        }).scroll();
    }

    var popUp = function() {
        $(".project__title").click(function(){
            var popupid = "#"+$(this).data("popupid");
            $(popupid).addClass("active");
        })

        $(".btnClose").click(function(){
            $(".project__popUp").removeClass("active");
        })
    }


	var init = function() {
        anchorSmoothScroll();
        anchorLoadSmoothScroll();
        titleVisible();
        topVisible();
        popUp();
	};


	$(document).ready(function() {
		init();
    });

})(jQuery);
